<template>
    <div style="overflow: auto">
        <section class="modals">
            <div class="container products">
                <div class="modal fade" id="paymentsModal" role="dialog" tabindex="-1" aria-hidden="true">
                    <div class="modal-dialog modal-lg">
                        <div class="modal-content unread-messages" ref="load_licence">
                            <button style="display: none" type="button" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                            <div class="modal-body">
                                <div class="chatting-page" style="min-height: 400px;">
                                    <div class="container">
                                        <div class="row">
                                            <div class="col-sm-12 col-md-12 col-lg-12">
                                                <div class="milestone-wrapper">
                                                    <div class="milestone">
                                                        <h3>Checkout Order</h3>
                                                        <br>
                                                        <p v-if="lastData === true" style="width: 100%;color: white;margin-top: 10px;text-align: left;padding: 10px" class="bg-warning border-warning">Loaded Last Data :: <button @click="clearLastOrder" class="btn btn-dark btn-sm">Reset Last Order</button></p>
                                                    </div>
                                                </div>
                                                <div class="db-table table-responsive">
                                                    <table class="table">
                                                        <thead>
                                                        <tr>
                                                            <th>Amount</th>
                                                            <th>Licence Name</th>
                                                            <th>Status</th>
                                                            <th>Action</th>
                                                        </tr>
                                                        </thead>
                                                        <tbody>
                                                        <tr v-for="(items, index) in paymentData" :key="index">
                                                            <td>{{items.totalAmount}} NGN</td>
                                                            <td>{{items.company_name}}</td>
                                                            <td>Pending</td>
                                                            <td>
                                                                <div v-if="getUserDetails.user.id !== undefined " class="btn-group">
                                                                    <Rave
                                                                            v-if="pay_status === 'Pay' "
                                                                            class="btn btn-secondary btn-sm"
                                                                            style-class="btn btn-secondary btn-sm"
                                                                            :email="email"
                                                                            :amount="amount"
                                                                            :reference="reference"
                                                                            :rave-key="raveKey"
                                                                            :callback="callback"
                                                                            :close="close"
                                                                            currency="NGN"
                                                                    >
                                                                        <i class="fas fa-credit-card"></i>
                                                                        Pay with Rave
                                                                    </Rave>
                                                                    <button v-else class="btn btn-secondary btn-sm" type="button">
                                                                        {{ pay_status }}
                                                                    </button>

                                                                    <button type="button" class="btn btn-sm btn-secondary dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                                        <span class="sr-only">Toggle Dropdown</span>
                                                                    </button>
                                                                    <div class="dropdown-menu">
                                                                        <a href="javascript:;" id="Payy" @click="setPaymentStatus('Pay')" class="dropdown-item">Pay</a>
                                                                        <a id="canclee" @click="setPaymentStatus('Cancel')" class="dropdown-item" href="javascript:;">Cancel</a>
                                                                    </div>
                                                                </div>
                                                            </td>
                                                        </tr>
                                                        <tr>
                                                            <td>Total: {{ amount }} </td>
                                                            <td></td>
                                                            <td></td>
                                                            <td></td>
                                                        </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>

                                            <div v-if="getUserDetails.user.id === undefined " class="col-sm-12 col-md-12 col-lg-12">
                                                <div class="userStuff">

                                                    <h3>Additional Information</h3>
                                                    <br>

                                                    <register v-if="setLogin === false"></register>
                                                    <login v-if="setLogin === true" @setLoginClick="() => { setLogin = true }"  ></login>

                                                </div>
                                            </div>

                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    import Rave from "@/components/payWithRave";
    import {mapState} from "vuex";
    import register from "../../components/Auth/register";
    import login from "../../components/Auth/login";
    export default {
        name: "PaymentsModal",
        data() {
            return {
                pay_status: "Pay",
                raveKey: "FLWPUBK_TEST-c91e7c30a78688fe9b0d4bfecdc6ae4c-X",
                email: "foobar@example.com",
                amount: 0,
                paymentData: {},
                processData: {},
                setLogin: false,
                lastData: false,
            }
        },
        components: { Rave, register, login },
        props: {
            data: Object,
        },
        computed: {
            ...mapState(["purchaseItems","user"]),
            reference(){
                let text = "";
                let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
                for( let i=0; i < 10; i++ )
                    text += possible.charAt(Math.floor(Math.random() * possible.length));
                return `ALG_${text}`;
            },
            getPurchaseItems() {
                return this.purchaseItems;
            },
            getUserDetails() {
                return this.user;
            },
        },
        async mounted() {
            this.paymentData = this.$props;
            eventBus.$on("totalAmount", val => {
                this.amount = parseInt(val, 10);
            })
            window.eventBus.$on('setLogin', val => {
                this.setLogin = val
            })
            window.eventBus.$on('lastData', val => {
                if (val === true) {
                    this.lastData = true
                }
            })
        },
        methods: {
            clearLastOrder() {
                if (this.$ls.clearStorage('localCartItem')) {
                    window.location.reload();
                }
            },
            setPaymentStatus(status) {
                this.pay_status = status;
            },
            async getPaymentData() {
                return this.paymentData.name;
            },
            showLoadingPayment() {
                let loader = this.$loader(this, this.$refs.paymentModal, {
                    loaderType: "spinner",
                });
                return loader;
            },
            callback: function(response){
                var responseValue = response.success;
                if(responseValue === true) {
                    // if payment is successfull close modal for now
                    // console.log(response);
                    if(this.$ls.clearStorage('transaction_data')) {
                        const reef = response.tx
                        const transactionData = {
                            txRef: reef.txRef,
                            orderRef: reef.orderRef,
                            charged_amount: reef.charged_amount,
                            raveRef: reef.raveRef,
                        }
                        this.$ls.setStorage('transaction_data', transactionData)
                    }
                    // this value is emitted to be listened on by the payWithRave page
                    // in order to close the payment modal box after x seconds
                    window.eventBus.$emit("close_payment", 500);
                    window.eventBus.$emit("showDocument", true);
                    $(".close").click();
                    this.$router.push('/licenceDocument');
                }
            },
            close: function(){
                console.log("Payment closed ");
                $(".close").click();
            },
        },
    }
</script>

