<template>
    <div>
        <form method="post" class="submit-info">
            <response-message :data="errorData"></response-message>
            <div class="need-info">
                <p class="info-title">Username</p>
                <input v-model="formModelRegister.username" autocomplete="username" id="username" name="username" type="text" class="form-control" placeholder="Set a username">
            </div>
            <div class="need-info">
                <p class="info-title">Email Address</p>
                <input v-model="formModelRegister.email" autocomplete="email" id="email" name="email" type="text" class="form-control" placeholder="Email Address">
            </div>
            <div class="need-info">
                <p class="info-title">Phone</p>
                <input v-model="formModelRegister.phone" autocomplete="phone" id="phone" name="phone" type="text" class="form-control" placeholder="Telephone Number">
            </div>
            <div class="need-info">
                <p class="info-title">Address</p>
                <textarea v-model="formModelRegister.address" autocomplete="address" id="address" name="address" type="text" class="form-control" placeholder="Address"></textarea>
            </div>
            <div class="need-info">
                <p class="info-title">Password</p>
                <p v-if="passwordVisibility === false" @click=" () => { passwordVisibility = true } " class="tooltip-icon"><a href="javascript:;"><i class="fas fa-eye"></i></a></p>
                <p v-else @click=" () => { passwordVisibility = false } " class="tooltip-icon"><a href="javascript:;"><i class="fas fa-eye-slash"></i></a></p>
                <input v-model="formModelRegister.password" autocomplete="password" id="password" name="password" :type=" passwordVisibility === false ? 'password' : 'text' " class="form-control" placeholder="password">
            </div>
            <div class="need-info">
                <p class="info-title">Confirm Password</p>
                <p v-if="passwordVisibility2 === false" @click=" () => { passwordVisibility2 = true } " class="tooltip-icon"><a href="javascript:;"><i class="fas fa-eye"></i></a></p>
                <p v-else @click=" () => { passwordVisibility2 = false } " class="tooltip-icon"><a href="javascript:;"><i class="fas fa-eye-slash"></i></a></p>
                <input v-model="formModelRegister.confirm_password" autocomplete="confirm_password" id="confirm_password" name="confirm_password" :type=" passwordVisibility2 === false ? 'password' : 'text' " class="form-control" placeholder="confirm password">
            </div>
            <div class="pick-category">
                <p class="info-title">Register as</p>
                <ul>
                    <li>
                        <div class="form-group select-work-type">
                            <select v-model="formModelRegister.user_type" class="custom-select">
                                <option value="1">Personal</option>
                                <option value="2">Agent</option>
                            </select>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="remember">
                <label class="checkBoxCont pull-left">
                    <p style="font-size: 12px">
                        By proceeding, you agree to our
                        <a href="#">Terms of Use</a> and confirm you have read our
                        <a href="#">Privacy Policy</a>.
                    </p>
                    <input type="checkbox" value="0"
                           v-model="formModelRegister.terms" />
                    <span class="checkmark"></span>
                </label>
                <a href="javascript:;" @click="setLogin" class="forgot pull-right"
                >Already have an account</a>
            </div>
            <div class="post-job-btn">
                <button @click.prevent="registerAccount" id="register-btn" type="submit" class="button-ymp">Register</button>
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        name: "register",
        data() {
            return {
                formModelRegister: {
                    email: "",
                    username: "",
                    password: "",
                    confirm_password: "",
                    user_type: "1",
                    terms: false,
                    address: "",
                    phone: "",
                },
                passwordVisibility: false,
                passwordVisibility2: false,
                errorData: {},
            }
        },
        mounted() {

        },
        methods: {
            setLogin() {
                // window.location.reload()
                window.eventBus.$emit('setLogin', true)
            },
            // clear form
            clearForm() {
              this.formModelRegister.email = "";
              this.formModelRegister.address = "";
              this.formModelRegister.phone = "";
              this.formModelRegister.password = "";
              this.formModelRegister.confirm_password = "";
              this.formModelRegister.username = "";
              this.formModelRegister.user_type = "1";
            },
            registerAccount() {
                if (this.validateForm()) {
                    if (!this.formModelRegister.terms) {
                        this.errorData = {
                            message: "Sorry you must accept our terms and conditions",
                            isError: true,
                        };
                    }else {
                        $("#register-btn").html('Processing...');
                        $("#register-btn").attr('disabled', true);

                        const requestPayload = {
                            email: this.formModelRegister.email,
                            password: this.formModelRegister.password,
                            password_confirmation: this.formModelRegister.confirm_password,
                            username: this.formModelRegister.username,
                            user_type: this.formModelRegister.user_type,
                        };

                        this.axios.post('/user/register',requestPayload)
                            .then(response => {
                                // console.log(response)
                                this.clearForm();
                                this.errorData = {
                                    message: "Registration Successful, please do login",
                                    isError: "success",
                                };
                                $("#register-btn").html('Register');
                                $("#register-btn").attr('disabled', false);
                            })
                            .catch(error => {
                                // console.log(error.response.data.message)
                                this.errorData = {
                                    message: error.response.data.data.password
                                        ?  error.response.data.message + ' ' + error.response.data.data.password
                                        : error.response.data.data.email
                                        ? error.response.data.message + ' ' + error.response.data.data.email
                                        : error.response.data.data.username
                                        ? error.response.data.message + ' ' + error.response.data.data.username
                                        : error.response.data.data
                                        ? error.response.data.message + ' ' + error.response.data.data
                                        : error.response.status === 400
                                            ? "Sorry an error occurred while registering, please contact the administrator <a href='mailto:support@matchdaydeals.com'>support@matchdaydeals.com</a>"
                                            : "Sorry an error occurred, check your internet",
                                    isError: true,
                                };
                                $("#register-btn").html('Register');
                                $("#register-btn").attr('disabled', false);
                            })
                    }
                }
            },
            validateForm() {
                if (!this.$validate("email", this.formModelRegister.email)) {
                    this.errorData = {
                        message: "Sorry please check your email again",
                        isError: true,
                    };
                    return false;
                }else if( this.formModelRegister.password.length < 5 ) {
                    this.errorData = {
                        message: "Sorry passwords must not be less than 5 characters",
                        isError: true,
                    };
                    return false;
                }else if(!this.$validate("username", this.formModelRegister.username)) {
                    this.errorData = {
                        message: "Sorry please check your username again",
                        isError: true,
                    };
                    return false;
                }
                else if(this.formModelRegister.confirm_password !== this.formModelRegister.password) {
                    this.errorData = {
                        message: "Sorry your passwords do not match",
                        isError: true,
                    };
                    return false;
                }
                else {
                    return true;
                }
            },
        },
    }
</script>

<style scoped>
    .submit-info {
        width: 100%;
        background-color: #fff;
        border-radius: 5px;
        box-shadow: 0 8px 12px rgba(0,0,0,.1);
        min-height: 500px;
    }
    .auth-title {
        text-align: center;
        margin: 20px 0;
        font-size: 26px;
        color: #6c757d;
        text-shadow: 0 7px 6px rgba(0,0,0,.1);
    }
</style>
