<template>
    <div>
        <section id="project-bid-page">
            <spinner :data="spinnerData"></spinner>
            <div class="container">
                <div class="bid-project-page" style="padding-bottom: 0">
                    <div class="row">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="bidding-title">
                                <h4>{{ productsForm.product_name }}</h4>
                            </div>
                        </div>
                    </div>
                    <div class="bidding-header">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="bidding-info">
                                <div class="row">
                                    <div class="col-sm-12 col-md-12 col-lg-8">
                                        <div class="bid-info">
                                            <div class="project-budget">
                                                <h5>Licence Price(NGN)</h5>
                                                <h4 class="project-amount">{{ totalAmount }}</h4>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-12 col-lg-4">
                                        <div class="get-button bidding">
                                            <button style="float: left;margin-right: 10px" data-toggle="modal" data-target="#paymentsModal" @click.prevent="purchaseItems()" class="get-btn">Purchase</button>
                                            <button style="float: left" class="get-btn">Add to cart</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div class="row">
                    <div class="col-lg-12 col-md-12 col-sm-12">
                        <div class="proposal-breakdown">
                            <div class="replicate">
                                <div class="row">
                                    <div class="col-lg-8 col-md-8 col-sm-12">
                                        <form id="licence-form">
                                            <response-message :data="errorData"></response-message>
                                            <div class="proposal-breakdown">
                                                <h4 class="title">Company</h4>
                                                <response-message v-bind:data="responseData" />
                                                <div class="form-group select-categories">
                                                    <input type="text" class="form-control description" :value="productsForm.company ? productsForm.company.company_name : ''" disabled >
                                                </div>
                                            </div>
                                            <br>
                                            <div class="replicate-inner clearfix" v-for="(requirements, index) in productsForm.requirements" :key="index">
                                                <div class="proposal-left" v-if="requirements.field_type === 'select'">
                                                    <p>{{ requirements.field_name }}</p>
                                                    <div class="form-group select-categories">
                                                        <select @change="calculate(requirements.field_id)" :name="'data_'+requirements.field_id" :id="'data_'+requirements.field_id" v-model="formModelKey" class="custom-select">
                                                            <option value="">None Selected</option>
                                                            <option v-for="(fdata, index) in requirements.field_data" :key="index" :value="[{ amount:fdata.amount, title:fdata.value }]" >{{ fdata.value }}</option>
                                                        </select>
                                                    </div>
                                                </div>
                                                <div class="proposal-left" v-else-if="requirements.field_type === 'text'">
                                                    <p>{{requirements.field_name}}</p>
                                                    <input type="text" class="form-control description " :id="'data_' + requirements.field_id"
                                                           :name="'data_' + requirements.field_id"
                                                           :required="requirements.field_required"
                                                           :placeholder="requirements.field_name"
                                                           v-model="formModel.requirements[requirements.field_id]"
                                                    >
                                                </div>

                                            </div>
                                            <div class="proposal-left " v-if="vehicles.status === true">
                                                <p>Select Manufacturer - <small style="font-size:66%;color:darkgray">{{selectedMake === '0' ? 'no vehicle make selected' : selectedMake }}</small></p>
                                                <input type="search" id="searchMake" name="searchMake" class="form-control description"
                                                       placeholder="Search Vehicle Make" autocomplete="off"
                                                       @keydown="searchVehicleMake" v-model="search_make" >
                                                <div class="search-results">
                                                    <div class="is_searching text-center p-2" v-if="isSearchingMake">
                                                        <i class="fa fa-spinner fa-spin"></i>
                                                    </div>
                                                    <span v-if="noMake === true">No vehicle manufacturer found</span>
                                                    <ul v-for="(resources,index) in vehicleMakeSearchResult" :key="index" class="results" id="results">
                                                        <li @click="selectVehicleMake(resources)">
                                                            {{ resources.make_name }}
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>

                                            <div class="proposal-left" v-if="selectedMake !== '0' ">
                                                <p>Select Vehicle - <small style="font-size:66%;color:darkgray">{{selectedVehicle}}</small></p>
                                                <input type="search" id="searchVehicle" name="searchVehicle" class="form-control description" placeholder="Search Vehicle" autocomplete="off" @keyup="searchVehicles" v-model="search" >
                                                <div class="search-results">
                                                    <div class="is_searching text-center p-2" v-if="isSearching">
                                                        <i class="fa fa-spinner fa-spin"></i>
                                                    </div>
                                                    <span v-if="noVehicle === true">No vehicle under manufacturer ({{selectedMake}}) found <code>{{ search }}</code></span>
                                                    <ul v-for="(resources,index) in vehicleSearchResult" :key="index" class="results" id="results">
                                                        <li @click="selectVehicle(resources)">
                                                            {{ resources.vehicle_name }}
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                    <div class="col-lg-4 col-md-4 col-sm-12">
                                        <div class="priceBox ">
                                            <article class="freelancers freelancer-grid-view">
                                                <div class="feature-image">
                                                    <img :src="productsForm.product_image" style="max-width: 120px" alt="">
                                                </div>
                                                <div class="project-id">
                                                    <h6 class="title">Category: {{ productsForm.category ? productsForm.category.category_name : '' }} </h6>
                                                </div>
                                                <div class="rate-hire">
                                                    <div class="rate">
                                                        <p>Price : <span>{{ totalAmount }}</span></p>
                                                    </div>
                                                    <div class="notify-tm">
                                                        <label class="checkbox-btn">I accept the terms and conditions stated <router-link to="">here</router-link>
                                                            <input v-model="checkBox" type="checkbox">
                                                            <span class="checkmark"></span>
                                                        </label>
                                                    </div>
                                                    <div class="hire-button" style="width: 100%;">
                                                        <button v-if=" formValidated === true " data-toggle="modal" data-target="#paymentsModal" style="margin-right: 10px" @click.prevent="purchaseItems()" class="hire-btn purchaseBtn">Purchase</button>
                                                        <button v-else style="margin-right: 10px" @click.prevent="purchaseItems()" class="hire-btn">Purchase</button>
                                                        <button @click="addToCart()" class="hire-btn">Add to Cart</button>
                                                    </div>
                                                </div>
                                            </article>

                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section id="followers">
            <div class="container">
            </div>
        </section>
        <PaymentsModal :data="paymentData" />
    </div>
</template>

<script>
    import $ from 'jquery';
    import PaymentsModal from "../../components/Modals/PaymentsModal";
    import {mapState} from "vuex";
    export default {
        name: "index",
        components: {PaymentsModal},
        data() {
            return {
                spinnerData: {},
                productsForm: {},
                totalAmount: 0,
                buttonPrice: "0",
                formModelKey: '',
                checkBox: true,
                formModelKeyData: {
                    requirement_id: '',
                    value: '',
                },
                formModel: {
                    requirements: [],
                    company: {
                        company_id: '',
                        company_name: '',
                    },
                },
                responseData: {},
                paymentData: {},
                vehicles: {
                    status: false,
                    vehicles: {},
                    last_page_url: "",
                    next_page_url: "",
                    total: "",
                    first_page_url: "",
                },
                search: "",
                search_make: "",
                selectedVehicle: "no vehicle selected",
                selectedMake: '0',
                selectedMake_id: '',
                selectedVehicle_id: '',
                vehicleSearchResult: {},
                vehicleMakeSearchResult: {},
                isSearching: false,
                isSearchingMake: false,
                noVehicle: false,
                noMake: false,
                formValidated: true,
                errorData: {},
            }
        },
        computed: {
            ...mapState(['user']),
            getUserDetails() {
                return this.user;
            },
            filteredResources() {
                var data = this.vehicles.vehicles;
                var query = this.search;

                var search = (key, inputArray) => {
                    for (let i=0; i < inputArray.length; i++) {
                        // return inputArray[i].vehicle_name;

                        return inputArray[i].vehicle_name.filter(function (item) {
                            return item;
                        })

                        // if (inputArray[i].vehicle_name.toLowerCase().includes(key)) {
                        //     return inputArray[i].vehicle_name;
                        // }
                    }
                };

                if (query) {
                    let resultObject = search(query, data);
                    // console.log('---- ',resultObject);
                    return resultObject;
                }else {
                    return null;
                }

            },
        },
        mounted() {
            $("#header").addClass("fixed-it");
            $("#header").addClass("sticky");
            this.getProductsForm();
            document.body.scrollTop = document.documentElement.scrollTop = 0;
            let val = this.$store.state.tempStore;
            // if (!val) {
            //     this.$router.push("/")
            // }

        },
        methods: {
            validateForm() {
                this.errorData = {};
                let req = this.formModel;
                let prodForm = this.productsForm.requirements;
                let data = [];
                if (this.formModel.requirements.length <= 0) {
                    this.errorData = {
                        message: "Sorry all fields must not be empty",
                        isError: true,
                    }
                    return false;
                }else {
                    let vehicle_required = this.productsForm;
                    vehicle_required = vehicle_required.vehicle_required;
                    for (let i = 0; i < prodForm.length; i++) {
                        // let tr = req.requirements[prodForm[i].field_id];
                        // var check = document.getElementById('data_' + prodForm[i].field_id);
                        let check = document.forms["licence-form"]['data_' + prodForm[i].field_id];
                        let vm_check, v_check;
                        if (vehicle_required) {
                            vm_check = document.forms["licence-form"]["searchMake"]
                            v_check = document.forms["licence-form"]["searchVehicle"]
                        }

                        if (check.value === "") {
                            // console.log('missing value ', check.id)
                            this.errorData = {
                                message: "Sorry field must not be empty",
                                isError: true,
                            }
                            $("#" + check.id).addClass("border-danger");
                            // let value_id = check.id;
                            // var numb = value_id.match(/\d/g);
                            // numb = numb.join("");
                            return false;
                        }else {
                            // console.log(check)
                            // if (!this.$validate('special',check.value)) {
                            //     this.errorData = {
                            //         message: "Sorry an error in field content",
                            //         isError: true,
                            //     }
                            //     $("#" + check.id).addClass("border-danger");
                            //     return false;
                            // }
                            $("#" + check.id).removeClass("border-danger");
                        }

                        if (vm_check.value === "") {
                            this.errorData = {
                                message: "Sorry do select a vehicle manufacturer",
                                isError: true,
                            }
                            $("#searchMake").addClass("border-danger");
                            return false;
                        }else {
                            $("#searchMake").removeClass("border-danger");
                        }
                        if (v_check.value === "") {
                            this.errorData = {
                                message: "Sorry do select a vehicle with make (" + this.selectedMake + ")",
                                isError: true,
                            }
                            $("#searchVehicle").addClass("border-danger");
                            return false;
                        }else {
                            $("#searchVehicle").removeClass("border-danger");
                        }

                    }
                }
                this.formValidated = true;
                return true;
            },
            getProductsForm() {
                let id = this.$route.params.id;
                let enc = this.$encrypto('D',id);
                this.axios.get('products/' + enc)
                    .then(response => {
                        // console.log(response.data.data.products);
                        this.productsForm = response.data.data.products;
                        this.formModel.company.company_id = response.data.data.products.company.company_id;
                        this.formModel.company.company_name = response.data.data.products.company.company_name;
                        this.totalAmount = response.data.data.products.product_value;
                        if (response.data.data.products.vehicle_required === true) {
                            this.vehicles.status = true;
                        }
                        this.checkLocalCartItem()
                    })
                    .catch(error => {
                        console.log(error)
                    })
                // var lstore = this.$ls.getStorage('productsForm');
                // lstore = lstore[0];
                // this.productsForm = lstore[0];
                // eventBus.$emit("totalAmount", this.totalAmount);
            },
            checkLocalCartItem() {
                if (this.$ls.checkStorage('localCartItem')) {
                    const products_id = this.productsForm.product_id;
                    const storedProducts_id = this.$ls.getStorage('localCartItem')[0].product_id;

                    if (storedProducts_id === products_id) {
                        $('.purchaseBtn').click()
                        this.purchaseItems()
                    }
                } else {
                    this.formValidated = false;
                }
            },
            // get all vehicles
            getVehicles() {
                this.axios.get('vehicles/10')
                    .then(response => {
                        console.log(response.data.data.vehicles)
                        this.vehicles.status = true;
                        this.vehicles.vehicles = response.data.data.vehicles.data;
                        this.vehicles.last_page_url = response.data.data.vehicles.last_page_url;
                        this.vehicles.next_page_url = response.data.data.vehicles.next_page_url;
                        this.vehicles.total = response.data.data.vehicles.total;
                        this.vehicles.first_page_url = response.data.data.vehicles.first_page_url;
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
            calculate(id) {
                var key = this.formModelKey;
                key = key[0].amount;

                if (key === null) {
                    this.formModelKeyData.requirement_id = id;
                    this.formModelKeyData.value = this.formModelKey[0].title;
                }else {
                    var initial = this.productsForm.product_value;
                    var sum = parseInt(initial) + parseInt(key);
                    this.totalAmount = sum;
                    this.formModelKeyData.requirement_id = id;
                    this.formModelKeyData.value = this.formModelKey[0].title;
                }
            },
            showLoading(options = {}) {
                let loader = this.spinnerData = {
                    spinner: 'Google',
                    isLoading: options.show !== undefined ? options.show : true ,
                    color: '#00bfff',
                    size: '60px',
                    options: 'center absolute',
                };
                return loader;
            },
            addToCart() {
                let user = this.$store.state.user.user;
                if (user === null) {
                    alert("Please Auth to Add to Cart")
                }else {

                }
            },
            // set the form fields as purchase items and open up the modal
            purchaseItems() {

                // set the stored purchase items and open up the modal.
                // this is so that if the page reloaded for any reason
                // the user data is still consistent
                if (this.$ls.checkStorage('localCartItem')) {
                    const cItem = this.$ls.getStorage('localCartItem')[0];
                    if (cItem.user_id === undefined) {
                        const userId = this.getUserDetails.user.id
                        const newCartItem = {
                            company_name: cItem.company_name,
                            company_id: cItem.company_id,
                            product_id: cItem.product_id,
                            user_id: userId,
                            data: cItem.data,
                            vehicle_id: cItem.vehicle_id,
                            totalAmount: cItem.totalAmount,
                        }
                        if(this.$ls.clearStorage('localCartItem')) {
                            this.$ls.setStorage('localCartItem', newCartItem)
                            const nCItem = this.$ls.getStorage('localCartItem')[0]
                            this.paymentData = nCItem;
                            window.eventBus.$emit("totalAmount", nCItem.totalAmount);
                            window.eventBus.$emit('lastData', true);
                            this.$store.commit("SET_PURCHASE_ITEMS",nCItem);
                        }
                    } else {
                        this.paymentData = cItem;
                        window.eventBus.$emit("totalAmount", cItem.totalAmount);
                        window.eventBus.$emit('lastData', true);
                        this.$store.commit("SET_PURCHASE_ITEMS",cItem);
                    }
                    // console.log('stored cart item', cItem)
                } else {
                    if (this.validateForm()) {
                        let cartItem;
                        var modelArr = this.formModel.requirements;
                        var formField = [];
                        var company_id = this.formModel.company.company_id;
                        var company_name = this.formModel.company.company_name;

                        for( var key in modelArr ) {
                            var value = modelArr[key];
                            var formFields = {
                                requirement_id: key,
                                value: value,
                            };
                            formField.push(formFields);
                        }

                        formField.push(this.formModelKeyData);

                        cartItem = {
                            company_name: company_name,
                            product_id: this.productsForm.product_id,
                            company_id: company_id,
                            data: formField,
                            user_id: this.getUserDetails.user.id,
                            vehicle_id: this.selectedVehicle_id,
                            totalAmount: this.totalAmount,
                        };

                        // loader = this.showLoadingCart();

                        this.paymentData = cartItem;
                        // store in locals storage
                        if(this.$ls.clearStorage("localCartItem")) {
                            this.$ls.setStorage("localCartItem",cartItem)
                        }
                        eventBus.$emit("totalAmount", this.totalAmount);
                        this.$store.commit("SET_PURCHASE_ITEMS",cartItem);

                    }
                }
            },
            selectVehicle(str) {
                this.selectedVehicle = str.vehicle_name;
                this.selectedVehicle_id = str.vehicle_id;
                this.search = str.vehicle_name;
                // this.search = "";
                this.vehicleSearchResult = {};
            },
            selectVehicleMake(str) {
                this.selectedMake = str.make_name;
                this.search_make = str.make_name;
                this.selectedMake_id = str.make_id;
                // this.search = "";*
                this.vehicleMakeSearchResult = {};
            },
            searchVehicleMake() {
                let query = this.search_make;
                if (query !== "") {
                    this.isSearchingMake = true;
                    this.axios.get(`makes/search/${query}/1000`)
                        .then(response => {
                            this.noMake = false;
                            this.isSearchingMake = false;
                            // console.log(response.data.data.makes.data);
                            this.vehicleMakeSearchResult = response.data.data.makes.data;
                        })
                        .catch(error => {
                            // console.log(error)
                            this.noMake = true;
                            this.isSearchingMake = false;
                            this.vehicleMakeSearchResult = {};
                            return error;
                        })
                }else {
                    this.isSearchingMake = false;
                    this.vehicleMakeSearchResult = {};
                }
            },
            searchVehicles() {
                let query = this.search;
                let make_id = this.selectedMake_id;
                if (query !== "") {
                    this.isSearching = true;
                    this.axios.get(`vehicles/search/makes/${make_id}/${query}/1000`)
                        .then(response => {
                            this.noVehicle = false;
                            this.isSearching = false;
                            // console.log(response.data.data.vehicles.data);
                            this.vehicleSearchResult = response.data.data.vehicles.data;
                        })
                        .catch(error => {
                            // console.log(error);
                            this.isSearching = false;
                            this.noVehicle = true;
                            this.vehicleSearchResult = {};
                            return error;
                        })
                }else {
                    this.isSearching = false;
                    this.vehicleSearchResult = {};
                }
            },

            findVehicless() {
                // here we create regular expressions for the search keywords
                // eslint-disable-next-line
                const acents = string => {
                    const mapAcents = {
                        a: /[\xE0-\xE6]/g,
                        e: /[\xE8-\xEB]/g,
                        i: /[\xEC-\xEF]/g,
                        o: /[\xF2-\xF6]/g,
                        u: /[\xF9-\xFC]/g,
                        c: /\xE7/g,
                    };
                    for(let letter in mapAcents) {
                        var regx = mapAcents[letter];
                        // replace the expressions
                        string = string.replace(regx, letter);
                    }

                    return string;
                };
                var data = this.vehicles.vehicles;
                var ul = document.getElementById("results");

                var render_lists = function(lists){
                    var li = "";
                    for(let index in lists){
                        li += "<li>" + lists[index] + "</li>";
                    }
                    ul.innerHTML = li;
                };

                render_lists(data);

                var input = this.search;

                var filterUsers = function(event){
                    keyword = input.value.toLowerCase();
                    filtered_users = data.filter(function(user){
                        user = user.toLowerCase();
                        return user.indexOf(keyword) > -1;
                    });

                    render_lists(filtered_users);
                };

                input.addEventListener('keyup', filterUsers);

                // var query = this.search;
                // query = acents(query.trim().toLowerCase());
                // var search = data.filter( query => {
                //     return (
                //         data.vehicle_name.toLowerCase().includes(query)
                //         // data.vehicle_make.toLowerCase().includes(query)
                //     );
                // });
                // this.vehicleSearchResult = search;
                // console.log(search)
            },
        },
    }
</script>

<style scoped>
.proposal-left {
    margin-bottom: 10px;
}
.replicate {
    position: relative;
    min-height: 500px;
}
    .priceBox {
        /*position: absolute;*/
        /*right: 0;*/
        /*top: 0;*/
        padding: 10px;
    }
    .search-results {
        width: 100%;
        float: left;
        padding:10px;
        border-radius:4px;
        max-height: 300px;
        overflow: auto;
    }
    .search-results .results {
        margin:0;
        padding:0 10px;
        list-style-type: none;
    }
    .search-results .results li {
        padding: 10px;
        background-color: #f8f8f8;
        margin-bottom: 5px;
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.3s ease-in;
    }
    .search-results .results li:hover {
        background-color: #00AEEF;
        color: #fff;
    }
</style>
